.card {
    background: #fff;
    border-radius: 2px;
    height: 175px;
    margin: 1rem;
    position: relative;
    width: 175px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
}

.card > .img-container {
    height: 100%;
    overflow: hidden;
    background:#6CADDC;
}

.card > .img-container > img {
    width: 100%
}

.card:hover {
    border-color: lightgreen;
    border-width: 2px;
}